    function intersectTriangle(ray, face, target) {
        // Compute the offset origin, edges, and normal.
		// from http://www.geometrictools.com/GTEngine/Include/Mathematics/GteIntrRay3Triangle3.h

		_edge1.subVectors(face.b, face.a);
		_edge2.subVectors(face.c, face.a);

		// Solve Q + t*D = b1*E1 + b2*E2 (Q = kDiff, D = ray direction,
		// E1 = kEdge1, E2 = kEdge2, N = Cross(E1,E2)) by
		//   |Dot(D,N)|*b1 = sign(Dot(D,N))*Dot(D,Cross(Q,E2))
		//   |Dot(D,N)|*b2 = sign(Dot(D,N))*Dot(D,Cross(E1,Q))
		//   |Dot(D,N)|*t = -sign(Dot(D,N))*Dot(Q,N)
		var DdN = ray.direction.dot(face.normal);

        if (DdN > 0)
            return null;
        else if (DdN < 0)
			DdN = -DdN;
        else
			return null;

		_diff.subVectors(ray.origin, face.a);
		var DdQxE2 = -ray.direction.dot(_edge2.crossVectors(_diff, _edge2));

		// b1 < 0, no intersection
		if ( DdQxE2 < 0 )
			return null;

		var DdE1xQ = -ray.direction.dot( _edge1.cross( _diff ) );

		// b2 < 0, no intersection
		if (DdE1xQ < 0)
			return null;

		// b1+b2 > 1, no intersection
		if (DdQxE2 + DdE1xQ > DdN)
    		return null;

		// Line intersects triangle, check if ray does.
		var QdN = _diff.dot(face.normal);

		// t < 0, no intersection
		if (QdN < 0)
			return null;

		// Ray intersects triangle.
		return ray.at(QdN / DdN, target);
    }

    function ascSort(a, b) {
        return a.distance - b.distance;
    }

    var _point = new Vector3();

    function rayBspNodeIntersect(ray, rayLength, start, end, node, intersects) {
        var plane, distS, distE, sideS = 0, sideE = 0;

        if (node.plane) {
            plane = node.plane;
            // distS = plane.normal.dot(start) + plane.constant;
            distS = (plane.normal.x * start.x + plane.normal.y * start.y + plane.normal.z * start.z) + plane.constant;
            // distE = plane.normal.dot(end) + plane.constant;
            distE = (plane.normal.x * end.x + plane.normal.y * end.y + plane.normal.z * end.z) + plane.constant;

            if (distS > coplanarTolerance)
                sideS = 1;
            else if (distS < -coplanarTolerance)
                sideS = -1;
            else
                sideS = 0;

            if (distE > coplanarTolerance)
                sideE = 1;
            else if (distE < -coplanarTolerance)
                sideE = -1;
            else
                sideE = 0;
        }

        // Check faces of this node if the ray crosses it or coplanar to its plane.
        if (node.faces && (sideS == 0 || sideE == 0 || sideS != sideE)) {
            for (var faceIndex = 0; faceIndex < node.faces.length; faceIndex++) {
                var face = node.faces[faceIndex];

                var intersect = intersectTriangle(ray, face, _point);
                if (!intersect)
                    continue;

                var distance = ray.origin.distanceTo(_point);
                if (distance < 0 || distance > rayLength)
                    continue;

                var hit = {
                    distance: distance,
                    point: _point.clone(),
                    object_index: face.object_index,
                    materialIndex: face.materialIndex,
                };

                intersects.push(hit);
            }
        }

        if (sideS > 0 && sideE > 0) {
            if (node.front)
                rayBspNodeIntersect(ray, rayLength, start, end, node.front, intersects);
        }
        else if (sideS < 0 && sideE < 0) {
            if (node.back)
                rayBspNodeIntersect(ray, rayLength, start, end, node.back, intersects);
        }
        else if (sideS != 0 || sideE != 0) {
            var t = distS / (distS-distE);
            var split = end.clone().sub(start).multiplyScalar(t).add(start);

            if (distS >= 0) {
                if (node.front)
                    rayBspNodeIntersect(ray, rayLength, start, split, node.front, intersects);
                if (node.back)
                    rayBspNodeIntersect(ray, rayLength, split, end, node.back, intersects);
            }
            else {
                if (node.front)
                    rayBspNodeIntersect(ray, rayLength, split, end, node.front, intersects);
                if (node.back)
                    rayBspNodeIntersect(ray, rayLength, start, split, node.back, intersects);
            }
        }
    }

    this.rayIntersect = function(start, end) {
        var intersects = new Array();

        var rayDirection = new Vector3();
        rayDirection.copy(end).sub(start);
        var rayLength = rayDirection.length();
        if (rayLength == 0)
            return true;
        rayDirection.divideScalar(rayLength);

        var ray = new Ray(start, rayDirection);
        // console.log(rootNode);
        rayBspNodeIntersect(ray, rayLength, start, end, rootNode, intersects);
        intersects.sort(ascSort);
        return intersects;
    }

